}




/* Enhance scroll style*/
/* width */
::-webkit-scrollbar {
  width: 5px;
  height: 5px
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px #ebe8e8;
  background: #ebe9e9;
}
/* Track */
::-webkit-scrollbar-track {
  -webkit-box-shadow: 0;
  border-radius: 0;
  background: #ffffff;
}




